@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&display=swap");
html {
  font-size: 15px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
  font-family: "Inter", sans-serif;
}

a {
  text-decoration: none;
  color: black;
  width: 100%;
}

button {
  cursor: pointer;
}

ul {
  list-style-type: none;
}

.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

body {
  background-color: hsl(36deg, 100%, 99%);
  padding: 1em 1.08em;
  color: hsl(240deg, 100%, 5%);
}

p {
  color: hsl(236deg, 13%, 42%);
}

header {
  height: 3.5em;
}
header img {
  height: 1.9em;
}
header .menu-button {
  cursor: pointer;
}
header .menu-button button {
  background: none;
  border: none;
  display: flex;
  background: transparent;
  outline: none;
  cursor: pointer;
}
header .menu-button button img {
  height: 1.3em;
}
header nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 68vw;
  height: 100vh;
  padding: 9.6em 0 0 1.5em;
  opacity: 0;
  transform: translateX(68vw);
  transition: transform 0.3s ease-in-out, box-shadow 0.4s ease-in-out;
  background-color: white;
  box-shadow: 1px 0px 0px 1px rgba(0, 0, 0, 0);
}
header nav #menu-close {
  background: transparent;
  border: none;
  outline: none;
  position: absolute;
  right: 1.6em;
  top: 2em;
  z-index: 99999;
  cursor: pointer;
}
header nav #menu-close img {
  height: 2rem;
}
header nav ul {
  gap: 1.8em;
}
header nav ul a {
  font-size: 1.15rem;
  cursor: pointer;
  color: hsl(240deg, 100%, 5%);
}
header nav ul a:hover {
  color: hsl(5deg, 85%, 63%);
}

.open {
  opacity: 1;
  transform: translateX(0);
  box-shadow: -200px 0px 0px 1px rgba(0, 0, 0, 0.451);
}

main .hero-container {
  gap: 4.2em;
  margin-top: 1.3em;
}
main .hero-container .hero .hero-text {
  margin-top: 1.2em;
  gap: 1.2em;
}
main .hero-container .hero .hero-text h1 {
  width: 90%;
  font-size: 2.7rem;
  line-height: 1;
  font-weight: 800;
}
main .hero-container .hero .hero-text .hero-text-button {
  gap: 1.1em;
}
main .hero-container .hero .hero-text .hero-text-button p {
  line-height: 1.76;
}
main .hero-container .hero .hero-text .hero-text-button button {
  margin-top: 0.5em;
  width: 185px;
  padding: 1.2em 0;
  height: auto;
  background-color: hsl(5deg, 85%, 63%);
  border: none;
  transition: background-color 150ms ease-in-out;
}
main .hero-container .hero .hero-text .hero-text-button button:hover {
  background-color: hsl(240deg, 100%, 5%);
}
main .hero-container .hero .hero-text .hero-text-button button a {
  text-transform: uppercase;
  color: white;
  font-weight: 700;
  letter-spacing: 5px;
}
main .hero-container .hero picture img {
  width: 100%;
}
main .hero-container aside {
  gap: 2em;
  padding: 1.4em 1.4em;
  background-color: hsl(240deg, 100%, 5%);
  color: white;
}
main .hero-container aside h2 {
  color: hsl(35deg, 77%, 62%);
  font-size: 2.1em;
}
main .hero-container aside article {
  padding-bottom: 2em;
  gap: 0.4em;
  border-bottom: 1px solid white;
}
main .hero-container aside article:nth-of-type(3) {
  border-bottom: none;
  padding-bottom: 0;
}
main .hero-container aside article h3 {
  font-size: 1.35rem;
  cursor: pointer;
  transition: color 150ms ease-in-out;
}
main .hero-container aside article h3:hover, main .hero-container aside article h3:active {
  color: hsl(35deg, 77%, 62%);
}
main .hero-container aside article p {
  line-height: 1.8;
}
main .featured-container {
  margin-top: 4.6em;
  gap: 2em;
}
main .featured-container .featured {
  gap: 1.5em;
}
main .featured-container .featured img {
  width: 100px;
}
main .featured-container .featured .featured-text h2 {
  font-size: 2em;
  color: hsl(233deg, 8%, 79%);
  cursor: pointer;
}
main .featured-container .featured .featured-text h3 {
  cursor: pointer;
  transition: color 150ms ease-in-out;
}
main .featured-container .featured .featured-text h3:hover, main .featured-container .featured .featured-text h3:active {
  color: hsl(5deg, 85%, 63%);
}
main .featured-container .featured .featured-text p {
  line-height: 1.5;
}

@media screen and (min-width: 1200px) {
  body {
    padding: 5.5em 11.5vw;
  }
  header img {
    height: 2.7em;
  }
  header nav {
    position: relative;
    top: auto;
    right: auto;
    width: auto;
    height: auto;
    padding: 0;
    opacity: 1;
    transform: translateX(0);
  }
  header nav button {
    display: none;
  }
  header nav ul {
    flex-direction: row !important;
    gap: 2.45em;
  }
  header nav ul a {
    color: hsl(236deg, 13%, 42%);
    font-size: 1.09rem;
  }
  header .menu-button {
    display: none;
  }
  main .hero-container {
    margin-top: 3.2em;
    gap: 2em;
    flex-direction: row !important;
  }
  main .hero-container .hero {
    margin-top: 0;
    gap: 2em;
    width: 50.7vw;
  }
  main .hero-container .hero picture img {
    width: 100%;
  }
  main .hero-container .hero .hero-text {
    flex-direction: row;
    margin: 0;
    gap: 0;
  }
  main .hero-container .hero .hero-text h1 {
    width: 55vw;
    max-width: 55vw;
    font-size: 3.75rem;
    font-weight: 800;
  }
  main .hero-container .hero .hero-text .hero-text-button {
    justify-content: space-between;
    width: 50vw;
    height: 180px;
  }
  main .hero-container .hero .hero-text .hero-text-button p {
    line-height: 1.6;
  }
  main .hero-container .hero .hero-text .hero-text-button button {
    margin: 0;
  }
  main .hero-container aside {
    margin: 0;
    padding: 2em 1.65em;
  }
  main .hero-container aside h2 {
    font-size: 2.6rem;
  }
  main .featured-container {
    flex-direction: row;
  }
  main .featured-container .featured {
    width: 500px;
  }
  main .featured-container .featured .featured-text h3 {
    font-size: 1.23rem;
  }
}